import { Component, h, Prop } from '@stencil/core';

const messageVariants = {
    base: 'flex p-[14px] text-dark text-left rounded-[0.3rem]',
    icon: {
        error: 'error',
        warning: 'warning',
        success: 'check_circle',
        info: 'info',
        default: '',
    },
    iconColour: {
        error: 'text-destructive-foreground',
        warning: 'text-warning-foreground',
        success: 'text-success-foreground',
        info: 'text-info-foreground',
        default: 'text-dark',
    },
    background: {
        high: {
            error: 'bg-destructive',
            warning: 'bg-warning',
            success: 'bg-success',
            info: 'bg-info',
            default: 'bg-surface-2',
        },
        low: '',
    },
};

/**
 * @category Feedback
 * @description A message component for displaying notifications, alerts, or informational messages.
 *
 * @slot - Placed after the content
 */
@Component({
    tag: 'atui-message',
    shadow: false,
})
export class AtuiMessage {
    /**
     * Type of the message.
     */
    @Prop()
    type: 'error' | 'warning' | 'success' | 'info' | 'default';

    /**
     * Theme of the message, either "light" or "default".
     */
    @Prop()
    impact: 'low' | 'high' = 'high';

    /**
     * Title of the app message.
     */
    @Prop()
    message_title: string;

    /**
     * An icon is provided for success, warning, error, or info types.
     * You can provide a custom value to replace the default icon.
     *
     * Must use material icon name.
     */
    @Prop()
    icon?: string;

    /**
     * Content of the message.
     */
    @Prop()
    content: string;

    get iconName(): string {
        if (this.icon) {
            return this.icon;
        } else {
            return messageVariants.icon[this.type];
        }
    }

    get hostClasses(): string {
        return `${messageVariants.base} ${messageVariants.background[this.impact][this.type]}`;
    }

    render() {
        return (
            <div
                class={this.hostClasses}
                data-name="message-container"
            >
                <i
                    class={`material-icons mr-8 text-[16px] ${messageVariants.iconColour[this.type]}`}
                    data-name="message-icon"
                >
                    {this.iconName}
                </i>

                <div class="w-full">
                    {this.message_title && (
                        <div
                            class="text-h6 mb-4 leading-normal font-medium"
                            data-name="message-title"
                        >
                            {this.message_title}
                        </div>
                    )}

                    {this.content && (
                        <div
                            class="!text-sm leading-normal"
                            data-name="message-content"
                        >
                            {this.content}
                        </div>
                    )}
                    <slot />
                </div>
            </div>
        );
    }
}
